import qs from 'qs'
import Login from '../../common/login.js'
import opBaidu from '../../components/w-pay/op-baidu.vue'
import opH5 from '../../components/w-pay/op-h5.vue'
export default {
	data() {
		return {
			mobile: '',
			orderList: [],
			userInfo: null,
			isEmpty: false
		};
	},
	components: {
		opBaidu,
		opH5
	},
	onShow() {

		swan.setPageInfo({
			title: '起网名_起游戏名_八字起名_周易取名_取名字_起名字大全',
			keywords: '起名,取名,起名字,取名字,宝宝起名,八字起名,周易起名,诗经起名,男孩起名,女孩起名',
			description: '起名网官方取名小程序，提供宝宝起名、男孩取名、女孩起名、周易起名、八字起名、诗经起名等智能起名字取名字服务。',
			articleTitle: '起名,取名,起名字,取名字,宝宝起名,八字起名,周易起名,诗经起名,男孩起名,女孩起名',
			releaseDate: '2019-05-15 12:01:30',
			image: '',
			video: '',
			visit: '',
			likes: '',
			comments: '',
			collects: '',
			shares: '',
			followers: '',
			success: function() {
				console.log('页面基础信息设置完成');
			}
		})


	},
	onLoad() {
		this.getUserInfo()
	},
	methods: {
		call() {
			console.log('call 18163728777')
			uni.makePhoneCall({
				phoneNumber: '18163728777'
			});
		},
		getUserInfo() {
			if (uni.getStorageSync('userInfo')) {
				this.userInfo = uni.getStorageSync('userInfo')
				uni.showLoading({
					title: '查询中'
				});
				uni.request({
					url: this.$store.state.baseUrl + '/api/netname/query',
					method: 'POST',
					data: {
						app_mchid: uni.getStorageSync('token'),
						app: 1
					},
					header: {
						"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
					},
					success: (res) => {
						console.log(res)
						uni.hideLoading()
						if (res.data.code == 1) {
							this.orderList = res.data.data.order_list
							if (this.orderList.length == 0) {
								this.isEmpty = true
							}
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: 'none'
							})
						}
					}
				});
			}
		},
		loginAuth() {
			Login().then(() => {
				this.getUserInfo()
			})
		},
		search() {
			this.orderList = null
			if (this.mobile == '') {
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none'
				})
			} else if (this.mobile.length != 11) {
				uni.showToast({
					title: '手机号格式不正确',
					icon: 'none'
				})
			} else {
				uni.showLoading({
					title: '查询中'
				});
				uni.request({
					url: this.$store.state.baseUrl + '/api/nameorder/order_query',
					method: 'POST',
					data: {
						mobile: this.mobile,
						app: 1
					},
					header: {
						"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
					},
					success: (res) => {
						uni.hideLoading()
						if (res.data.data.length > 0) {
							this.orderList = res.data.data
						} else {
							uni.showToast({
								title: '未查询相关到信息',
								icon: 'none'
							})
						}
					}
				});
			}
		},
		orderNext(i) {
			let params = {
				order_sn: this.orderList[i].order_sn
			}
			uni.navigateTo({
				url: '../webview/webview?' + qs.stringify(params)
			});

		}
	}
}
