export default {
	props: {
		orderSn: String
	},
	methods: {
		pay() {
			uni.request({
				url: this.$store.state.baseUrl+'/v2/api/nameorder/orderpay',
				methods: 'GET',
				data: {
					order_sn: this.orderSn,
					app: 1,
					is_h5: 1
				},
				success: (res) => {
					location.href=res.data.data.result
				}
			})
		}
	}
}
